"use client"

import { Button, Flex, Table, Tooltip } from "antd"
import React, { useEffect } from "react"
import { Ticket } from "../../../shared/entities/Ticket/Ticket"
import { getTicketList } from "../api"
import { useSearchParams } from "next/navigation"
import { AppTag } from "../../../shared/ui/AppTag/ui"
import { StatusEnum } from "../../../shared/entities/Ticket/StatusEnum"
import { Prove } from "../../../shared/entities/Prove/Prove"
import Image from "next/image"
import { CheckCircleOutlined, DeleteOutlined } from "@ant-design/icons"
import { GetTicketsResponse } from "../types"
import dayjs from "dayjs"
import { ReasonEnum } from "../../../shared/entities/Ticket/ReasonEnum"
import { User } from "../../../shared/entities/User/User"
import Link from "next/link"
import { translateReason, translateStatus } from "../utils"

interface TicketTableProps {
  data: GetTicketsResponse
}

const columns = [
  {
    title: "Студент",
    dataIndex: "user",
    key: "user",
    renderItem: (user: User) => {
      return <Link href={`/home/users/${user.id}`}>{user.name}</Link>
    },
  },
  { title: "Название", dataIndex: "name", key: "name" },
  {
    title: "С даты",
    dataIndex: "startDate",
    key: "dateFrom",
    renderItem: (text: string) => {
      return dayjs(text).format("dd.mm.yyyy")
    },
  },
  {
    title: "По дату",
    dataIndex: "endDate",
    key: "dateTo",
    renderItem: (text: string) => {
      return dayjs(text).format("DD.MM.YYYY")
    },
  },
  {
    title: "Статус",
    dataIndex: "status",
    key: "status",
    renderItem: (text: StatusEnum) => {
      return (
        <AppTag variant={text === StatusEnum.PENDING ? "default" : text === StatusEnum.APPROVED ? "success" : "danger"}>
          {translateStatus[text]}
        </AppTag>
      )
    },
  },
  {
    title: "Причина",
    dataIndex: "reason",
    key: "reason",
    renderItem: (text: ReasonEnum) => {
      return <AppTag variant='info'>{translateReason[text]}</AppTag>
    },
  },
  {
    title: "Подтверждения",
    dataIndex: "prooves",
    key: "prooves",
    renderItem: (prooves: Prove[]) => {
      return (
        <>
          {prooves.map(item => {
            return (
              <Image
                style={{ width: 50, height: 50 }}
                key={item.id}
                src={item.path}
                alt='empty'
              />
            )
          })}
        </>
      )
    },
  },
  {
    title: "Действия",
    key: "actions",
    renderItem: ({ id }: Ticket) => {
      return (
        <Flex
          justify='center'
          gap={"12px"}
        >
          <Tooltip title='Быстрое подтверждение'>
            <Button
              onClick={() => console.log(`Approve ticket with id: ${id}`)}
              type='primary'
              size='small'
            >
              <CheckCircleOutlined />
            </Button>
          </Tooltip>
          <Tooltip title='Быстрое отклонение'>
            <Button
              onClick={() => console.log(`Approve ticket with id: ${id}`)}
              color='danger'
              type='primary'
              size='small'
            >
              <DeleteOutlined />
            </Button>
          </Tooltip>
        </Flex>
      )
    },
  },
]

export const TicketTable: React.FC<TicketTableProps> = ({ data }) => {
  const [dataSource, setDataSource] = React.useState<Ticket[]>(data.tickets || [])
  console.log("TicketTable dataSource", dataSource)
  const [isLoading, setIsLoading] = React.useState(false)

  const searchParams = useSearchParams()

  const fetchTickets = async () => {
    setIsLoading(true)
    getTicketList({
      userName: searchParams.get("userName") || undefined,
      startDate: searchParams.get("startDate") || undefined,
      endDate: searchParams.get("endDate") || undefined,
      limit: searchParams.get("limit") ? Number(searchParams.get("limit")) : 100,
      offset: searchParams.get("offset") ? Number(searchParams.get("offset")) : 0,
    })
      .then(response => {
        setDataSource(response.data.tickets)
      })
      .catch(error => {
        console.error("Error fetching tickets:", error)
      })
      .finally(() => {
        setIsLoading(false)
      })
  }
  useEffect(() => {
    fetchTickets()
  }, [
    searchParams.get("userName"),
    searchParams.get("limit"),
    searchParams.get("offset"),
    searchParams.get("endDate"),
    searchParams.get("startDate"),
  ])

  return (
    <Table
      loading={isLoading}
      columns={columns}
      dataSource={dataSource}
      rowKey='id'
      pagination={false}
    />
  )
}
